/* accordian */
.accordian_sec{
  background-color: transparent!important;
  border-color: transparent!important;
}
.accordian_border{
 border-top: 1px solid  #dee2e6!important;
 border-radius: 0.375rem!important;
 box-shadow: none!important;
}
/*  */
/* navbar */
.input-change {
  box-shadow: rgba(28, 28, 28, 0.08) 0px 2px 8px;
  border: 1px solid rgb(232, 232, 232);
  border-radius: 0.5rem;
  background-color: #fff;
}

::placeholder {
  font-size: 14px;
}

.text-style {
  font-size: 1.1rem;
  color: rgb(105, 105, 105);

}

.text-dec {
  padding: 3px;
  font-size: 0.8rem;
  color: rgb(130, 130, 130);
}

.text-dec:hover {
  color: rgb(239, 79, 95);
}

.circle {
  background-color: rgb(229, 243, 243);
  width: 4rem;
  height: 4rem;
  border-radius: 50%;
  align-items: center;
  display: flex;
  justify-content: center;
}

.text-heading {
  font-weight: 500;
  font-size: 1.3rem;
  color: rgb(239, 79, 95);
  align-items: center;
  display: flex;
  justify-content: center;
}

.circle-sec {
  background-color: rgb(248, 248, 248);
  width: 4rem;
  height: 4rem;
  border-radius: 50%;
  align-items: center;
  display: flex;
  justify-content: center;
}

.text-heading-2 {
  font-weight: 400;
  font-size: 1.1rem;
  color: #696969;
  align-items: center;
  display: flex;
  justify-content: center;
}

/* collection */
.primary-bg {
  background: rgb(248, 248, 248);
}

.image-overlay {
  position: relative;

}

.collection_img {
  height: 320px;
  width: 260px;
}

.image-inner {
  position: absolute;
  color: rgb(249, 246, 246);
  font-weight: 500;
  font-size: 18px;
  bottom: 8px;
  left: 16px;
}

.overlay {
  height: 320px;
  width: 260px;
  bottom: 0;
  left: 0;
  position: absolute;
  background: linear-gradient(0deg, rgba(0, 0, 0, 0.9) 0%, rgba(0, 0, 0, 0.05) 50%, rgba(0, 0, 0, 0.05) 85%);
}

.text-chng {
  font-size: 16px;
  color: rgb(239, 79, 95);

}

/*  */
/* section */
.sec-list {
  border: 1px solid rgb(207, 207, 207);
  color: rgb(156, 156, 156);
  background-color: rgb(255, 255, 255);
  border-radius: 0.5rem;
  padding: 0.5rem;
}

/*  */
/* treading */
.gap {
  gap: 15px;
}

.promoted-sec {
  position: absolute;
  background: rgba(0, 0, 0, 0.3);
  color: rgb(255, 255, 255);
  padding: 0px 2px;
  top: 10px;
  left: 10px;
  font-size: 10px;
}

.rating-text {
  font-size: 12px;
}

.image-inner2 {
  position: absolute;
  border-radius: 5px;
  color: rgba(249, 246, 246, 0.887);
  font-weight: 500;
  font-size: 18px;
  bottom: 18px;
  left: 0;
}

.bg-color {
  padding: 4px 60px 4px 14px;
  background: linear-gradient(90deg, rgb(17, 85, 203) 0%, rgb(83, 140, 238) 25%, rgb(17, 85, 203) 50%, rgba(17, 85, 203, 0.8) 80%, rgba(17, 85, 203, 0) 100%);
  ;
}

.bg-common {
  background-color: rgb(38, 126, 62);
  color: aliceblue;
  border-radius: 15%;
  width: 37px;
  height: 22px;
}

.bg {
  width: 368px;
}

.bg2 {
  width: 328px;
}

.shadow-effect:hover {
  width: 400px;
  /* background-color: black; */
  height: 360px;
  border-radius: 10px;
  border: 1px solid rgb(232, 232, 232);
  box-shadow: rgba(99, 99, 99, 0.2) 0px 2px 8px 0px;
}

.heading-primary {
  font-size: 15px;
  color: rgb(105, 105, 105);
}

.heading-sec {
  color: rgb(156, 156, 156);
  font-size: 15px;
}

.distance-heading {
  color: rgb(54, 54, 54);
  font-size: 15px;
}

/* */
/* accordian */
.main-heading {
  color: rgb(54, 54, 54);
  font-weight:500
}

.content-text {
  text-decoration: none;
  color: rgb(130, 130, 130);
  font-size: 17px;
  line-height: 1.8;
}

.accordion-heading {
  font-weight: 400;
  color: rgb(28, 28, 28);
  font-size: 22px;
}

.footer-para {
  color: rgb(130, 130, 130);
  font-size: 16px;
}

