  font-size: 14px;
  color: rgb(130, 130, 130);
}

.sizeing {
  width: 340px;
  box-shadow: rgba(28, 28, 28, 0.08) 0px 1.42623px 2.85246px;
  border: 1px solid rgb(232, 232, 232);
  padding: 10px 14px;
}

/* home-page */
 .hero_page {
  position: relative;
  text-align: center;
  color: white;
}
.hero_pageimg {
  height: 70vh;
  object-fit: cover;
  position: relative;
  z-index: -1;
}
.bg-heroimg {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
}
.modal_bg{
  background-color: rgb(28, 28, 28);
  opacity: 0.85;
}
/* form */
.btn_logo{
  width: 100px;
  height: 105px;
}
.modal-body a{
  color: #ef4f5f;
  font-size: 14px;
  text-decoration:none;
}
.btn-color{
  background-color:  #ef4f5f!important;
  color: #EEE;
}
.label_txt{
  font-size: 14px;
}
.login{
  background-color:  #ef4f5f!important;
  padding: 10px 10px;
  color: #EEE;
}
.text-form{
  font-size: 14px;
}
.input_form{
  padding: 10px 10px;
}
.input_form2{
  padding: 14px 14px;
}
/*  */
.center_text {
z-index: -1;
  position: absolute;
  top: 60%;
  left: 50%;
  transform: translate(-50%, -50%);
  text-align: center;
  color: white;
}
.hero_page .form-control {
  background-color: rgba(255, 255, 255, 0.8);
}
.navbar-toggler {
  border-color: rgba(255, 255, 255, 0.1);
}

.heropage-logo {
  width: 280px;
}

.text_primary{
  font-size: 18px;
}
.text_primary1{
  font-size: 16px;
}


.cards {
  width: 22rem;
}

/*  */
.card {
  transition: transform 0.3s ease-in-out;
}

.hover-scale:hover {
  transform: scale(1.05);
}

/* get zomato */
.para {
  font-size: 16px;
  color: #363636;
}

