.zomato-data {
  position: relative;
}

.zomato-data_overlay {
  position: absolute;
  right: 20px;
  top: 35px;
}

.path {
  background-attachment: fixed;
}

.line_de {
  width: 260px;
  border-top: 1px solid rgb(232, 232, 232);
  border-right: none;
  border-left: none;
  border-bottom: none;
  border-radius: 0.6rem 0.6rem 0px 0px;
}

.line_se {
  width: 260px;
  border-top: 1px solid rgb(232, 232, 232);
  border-right: none;
  border-left: none;
  border-bottom: none;
  border-radius: 0.6rem 0.6rem 0px 0px;
}

.menu_text {
  font-size: 13px;
  color: #696969;
}

.section_txt {
  text-decoration: none;
  border: 1px solid rgb(207, 207, 207);
  border-radius: 59px;
  padding: 0.5rem;
  color: rgb(17, 145, 153);
  cursor: pointer;
  margin: 0px 20px 0px 0px;

}

.box_shadow {
  box-shadow: rgba(28, 28, 28, 0.12) 0px 2px 8px;
  width: 400px;
}

/* text-underline iconsssssssss */
.c-title {
  position: relative;
}

.c-title::after {
  content: "";
  width: 110%;
  height: .2em;
  background: rgb(239, 79, 95);

  position: absolute;
  bottom: -17px;
  left: 0;

}

.c-title2 {
  position: relative;
}

.c-title2::after {
  content: "";
  width: 110%;
  height: .2em;
  background: rgb(239, 79, 95);
  position: absolute;
  bottom: -17px;


}

.c-title3 {
  position: relative;
}

.c-title3::after {
  content: "";
